body, body * {
  --primary: #fce;
  --shaded-primary: #c069a3;
  --on-primary: black;
  --secondary: #666;
  --shaded-secondary: #3f3f3f;
  --on-secondary: white;
  --tertiary: #eee;
  --shaded-tertiary: #b3b3b3;
  --on-tertiary: black;
  --background: white;
  --shaded-background: white;
  --on-background: black;
  --positive: #16c24a;
  --shaded-positive: #158a38;
  --on-positive: white;
  --negative: #c22929;
  --shaded-negative: #882424;
  --on-negative: white;
  margin: 0;
  padding: 0;
  font-family: Trebuchet MS, Lucida Sans Unicode, Lucida Grande, Lucida Sans, Arial, sans-serif;
  font-size: 20px;
  font-weight: normal;
  display: block;
}

body {
  background-color: var(--background);
  width: 100%;
  height: max-content;
}

header {
  background-color: var(--primary);
  justify-content: space-between;
  align-items: center;
  margin-bottom: .5em;
  padding: 1em;
  display: flex;
}

h1 {
  font-size: 1.4em;
  font-weight: bold;
}

input[type="text"] {
  border-style: solid;
  border-width: .2em;
  border-color: var(--primary);
  border-radius: .5em;
  padding: .2em;
}

.invalidbox {
  background-color: var(--negative);
  border-color: var(--shaded-negative);
  color: var(--on-negative);
  border-style: solid;
  border-width: .2em;
  border-radius: .5em;
  padding: .2em;
}

input[type="text"]:user-invalid {
  border-color: var(--shaded-negative);
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

input[type="text"] + .invalidbox, select + .invalidbox {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  display: none;
}

input[type="text"]:user-invalid + .invalidbox {
  display: block;
}

select:user-invalid + .invalidbox {
  display: block;
}

select {
  background-color: #fff;
}

input[type="range"] {
  appearance: none;
  cursor: pointer;
  background: none;
  width: 100%;
}

input[type="range"]::-webkit-slider-runnable-track {
  background: var(--primary);
  border-color: var(--shaded-primary);
  border-style: solid;
  border-radius: .4em;
  height: .6em;
}

input[type="range"]::-moz-range-track {
  background: var(--primary);
  border-color: var(--shaded-primary);
  border-style: solid;
  border-radius: .4em;
  height: .6em;
}

input[type="range"]::-webkit-slider-thumb {
  appearance: none;
  margin-top: -.6em;
}

input[type="range"]::-moz-range-thumb {
  border: none;
  border-radius: 0;
}

input[type="range"]::-webkit-slider-thumb {
  background-color: var(--primary);
  border-color: var(--shaded-primary);
  border-style: solid;
  border-radius: 1em;
  width: 1.5rem;
  height: 1.5rem;
}

input[type="range"]::-moz-range-thumb {
  background-color: var(--primary);
  border-color: var(--shaded-primary);
  border-style: solid;
  border-radius: 1em;
  width: 1.5rem;
  height: 1.5rem;
}

button, button.primary {
  border-style: none;
  border-color: unset;
  background-color: var(--primary);
  color: var(--on-primary);
  border-radius: 1em;
  padding: 1em;
}

button:active:hover, button.primary:active:hover {
  background-color: var(--shaded-primary);
}

button.secondary {
  background-color: var(--secondary);
  color: var(--on-secondary);
}

button.secondary:active:hover {
  background-color: var(--shaded-secondary);
}

.flex {
  display: flex;
}

.flex.center {
  justify-content: center;
  align-items: center;
}

.flex.row {
  flex-direction: row;
}

.flex.column {
  flex-direction: column;
}

.flex.gap-sm {
  gap: .5em;
}

.flex.gap-md {
  gap: 1em;
}

.flex.gap-lg {
  gap: 3em;
}

.spacer {
  flex: 1;
}

.infobox {
  background-color: var(--background);
  color: var(--on-background);
  border-width: 2px;
  border-color: var(--shaded-background);
  border-style: solid;
  padding: 1em;
}

.error {
  background-color: var(--negative);
  color: var(--on-negative);
}

.box {
  background-color: var(--tertiary);
  color: var(--on-tertiary);
  border: 2px solid #000;
  border-radius: 1em;
  gap: 1em;
  padding: 1em;
}

.rating_display {
  flex-direction: row;
  justify-content: space-evenly;
  display: flex;
}

.rating_display > * {
  font-size: 2em;
}
